@font-face {
  font-weight: normal;
  font-family: "Roboto Condensed";
  font-style: normal;
  src: url("../fonts/RobotoCondensed-Regular.woff") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff2") format("woff"); }

@font-face {
  font-weight: bold;
  font-family: "Roboto Condensed";
  font-style: normal;
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff2") format("woff"); }

@font-face {
  font-weight: normal;
  font-family: "Roboto";
  font-style: normal;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"); }

@font-face {
  font-weight: 500;
  font-family: "Roboto";
  font-style: normal;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff"); }

@font-face {
  font-weight: normal;
  font-family: "PT Mono";
  font-style: normal;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"); }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  font-family: "Roboto Condenced", "Arial", sans-serif; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.button {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 10px;
  font-weight: bold;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: -0.06em;
  border-radius: 5px; }

.button--transparent {
  background-color: transparent; }

.container {
  max-width: 1420px;
  margin: 0 auto;
  padding: 0 20px; }

@media screen and (max-width: 1024px) {
  .container {
    max-width: 720px; } }

@media screen and (max-width: 768px) {
  .container {
    min-width: 290px;
    padding: 0 15px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 10px; }

.logo__image {
  width: 52px;
  height: 52px;
  margin-right: 14px; }

.logo__text-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-top: 7px; }

.logo__main-text {
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  color: #ffffff;
  letter-spacing: -0.1em; }

.logo__add-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #788891;
  font-style: normal; }

@media screen and (max-width: 1024px) {
  .logo {
    padding-left: 0; } }

@media screen and (max-width: 768px) {
  .logo__image {
    width: 38px;
    height: 38px;
    margin-right: 14px; }
  .logo__text-wrapper {
    display: none; } }

.promo {
  padding-top: 33px;
  background: url("../img/promo-bg.png") center no-repeat #1b2d37; }

@media screen and (max-width: 1024px) {
  .promo {
    padding-top: 19px; } }

@media screen and (max-width: 768px) {
  .promo {
    padding-top: 16px; } }

.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.header__info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0;
  margin-right: 52px;
  margin-left: auto;
  padding: 0;
  list-style: none; }

.header__info li {
  margin-right: 88px; }

.header__info li.last {
  margin-right: 0; }

.header__link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 16px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #ffffff; }

.header__link svg {
  margin-right: 16px;
  fill: #00b2ff; }

.header__button {
  min-width: 180px;
  padding-top: 14px;
  padding-bottom: 14px;
  line-height: 16px;
  border: 1px solid #ffffff; }

@media screen and (max-width: 1024px) {
  .header__info {
    margin-right: 0; }
  .header__info li {
    margin-right: 33px; }
  .header__link svg {
    margin-right: 0; }
  .header__link--mail svg {
    width: 25px;
    height: 19px; }
  .header__link--phone svg {
    width: 24px;
    height: 24px; }
  .header__link {
    font-size: 0; }
  .header__button {
    display: none; } }

@media screen and (max-width: 768px) {
  .header__info li {
    margin-right: 23px; } }
